import React from "react"
// import icon from "./../icon.png"
import { Link } from "gatsby"
// import "./layout.scss"

const ListLink = props => (
  <li>
    <Link to={props.to} className="header-links" activeStyle={{ color: '#333' }}>{props.children}</Link>
  </li>
)

export default ({children}) => (
  <body>
    <div id="page-container">
      <div id="content-wrap">
          <div className="title"><img src="https://imgur.com/YzY5UEZ.png" alt="Icon"></img></div>
          <div className="header-links-container">
            <nav> <ul>
              <ListLink to="/">HOME</ListLink>
              <ListLink to="/about/">ABOUT</ListLink>
              <ListLink to="/webdev/">WEBDEV</ListLink>
              <ListLink to="/gamedev/">GAMEDEV</ListLink>
              {/* <ListLink to="/Art/">ART</ListLink> */}
            </ul> </nav>
          </div>
        <div>{children}</div>
      </div>
    <footer id="footer">Â© 2019 Tina Peng</footer>
    </div>
  </body>
)